body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@-webkit-keyframes bounce {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0); }
  30% {
    opacity: 1;
    -webkit-transform: translate3d(0, 8px, 0);
            transform: translate3d(0, 8px, 0); }
  60% {
    -webkit-transform: translate3d(0, -2px, 0);
            transform: translate3d(0, -2px, 0); }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0); } }

@keyframes bounce {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0); }
  30% {
    opacity: 1;
    -webkit-transform: translate3d(0, 8px, 0);
            transform: translate3d(0, 8px, 0); }
  60% {
    -webkit-transform: translate3d(0, -2px, 0);
            transform: translate3d(0, -2px, 0); }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0); } }

.toast {
  position: fixed;
  z-index: 9999;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: -webkit-flex;
  display: flex; }
  .toast.no-mask {
    pointer-events: none; }
  .toast-container {
    margin: auto;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0); }
    .toast-container.top {
      margin-top: 15%; }
    .toast-container.bottom {
      margin-bottom: 15%; }
  .toast-content {
    max-width: 200px;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 14px;
    line-height: 1.4;
    text-align: center;
    word-break: break-all;
    color: #fff;
    background: rgba(20, 22, 26, 0.8); }
    .toast-content > img {
      width: 40px;
      height: 40px;
      margin: 5px 0; }
      .toast-content > img.spin {
        -webkit-animation: spin 1s infinite linear;
                animation: spin 1s infinite linear; }
  .toast-bounce-appear {
    -webkit-animation: bounce .3s ease-in-out;
            animation: bounce .3s ease-in-out; }
  .toast-bounce-exit {
    opacity: 1; }
    .toast-bounce-exit-active {
      opacity: 0;
      -webkit-transform: translate3d(0, -100%, 0);
              transform: translate3d(0, -100%, 0);
      -webkit-transition: .3s;
      transition: .3s; }
  .toast-fade-appear {
    opacity: 0; }
    .toast-fade-appear-active {
      opacity: 1;
      -webkit-transition: .3s;
      transition: .3s; }
  .toast-fade-exit {
    opacity: 1; }
    .toast-fade-exit-active {
      opacity: 0;
      -webkit-transition: .3s;
      transition: .3s; }

.custom-toast-loading {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: url(/toast/static/media/loading.e0d20777.gif) no-repeat center/contain #fff;
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.16); }

.custom-toast-content {
  padding: 12px 25px;
  border-radius: 21px;
  font-size: 13px;
  color: #fff;
  background: #00a0f0;
  box-shadow: 0 4px 8px 0 rgba(0, 160, 240, 0.4); }

.top{
  margin-bottom: 30px;
  padding: 10px;
  text-align: center;
}

button{
  display: block;
  width: 180px;
  height: 40px;
  margin: 20px auto;
  background: #f5f5f5;
  color: #666;
  border: 1px solid #ddd;
  outline: none;
}
